import React,{useState,useEffect} from 'react';
import {NavLink} from 'react-router-dom';
import { Layout, Image, Row, Col, Space, Button, Avatar, Typography, Tabs } from 'antd';
import logo from '../../assets/img/icons/logo_horizontal.png'
import {PlusOutlined, BarsOutlined, UserOutlined, RiseOutlined, QuestionOutlined } from '@ant-design/icons';
import './deals.scss';


export const Deals:React.FC = () => {
    const [resize,setResize] = useState(false);
    const [position,setPosition]:any = useState("top")
    const {Header,Content} = Layout;
    const {Title, Paragraph} = Typography;
    const {TabPane} = Tabs;
    const resizeScreen = () => {
        if(window.innerWidth <= 500){
            setResize(true);
            setPosition("bottom");
        }else{
            setResize(false);
            setPosition("top");
        }
    }
    useEffect(()=>{},[position])
    useEffect(() => {
        resizeScreen()
        window.addEventListener('resize',resizeScreen);
        return ()=>{
            window.removeEventListener('resize',resizeScreen);
        }
    })

    return (
            <Layout className='bg-white'>
                <Content>
                    <Row justify='center'>
                        <Col xxl={24} xl={24} md={24} sm={24} xs={24}>
                    <Tabs tabPosition={position} type='card'  className={`${!resize ? 'end' :'center'}`} tabBarExtraContent={!resize ? {left: <Image src={logo} preview={{visible:false, mask:false}} /> , right:<Button icon={<PlusOutlined style={{fontSize:"22px"}}/>}  className='btn-deals'>New Deal</Button>}: null}>
                        <TabPane tab={<span style={{display:'flex',flexDirection:'column'}}> {resize ? <BarsOutlined />: ''}  <div style={resize ? {fontSize:'10pt'}:{}}>Deals</div></span>} key='1' className='uppercase'>
                        <Row justify='center' className='offset-top-2'>
                            
                        <Col xxl={16} sm={16} xl={16} xs={24} className={`${resize === false ? "" : "padding-left-1 padding-right-1"}`}>
                            <div className='medium-line-medium-w-600 dark-blue'>
                                Deals
                            </div>
                        </Col>
                        <Col xxl={16} sm={24} xl={24} xs={24} style={{textAlign:'center'}} className='offset-top-8'>
                            <Space direction='vertical'>
                                <Avatar size={resize=== false ? 270 : 210} />
                                <Title className='normal-line-normal-w-600 dark-blue'>User Name</Title>
                                <Paragraph className='body-line-normal-w-500 dark-blue'>Welcome on board! Register new deal</Paragraph>
                            </Space>
                        </Col>
                       
                    </Row>
                        </TabPane>
                        <TabPane tab={<span style={{display:'flex',flexDirection:'column'}}> {resize ? <QuestionOutlined />: ''}  <div style={resize ? {fontSize:'10pt'}:{}}>Help</div></span>} key='2'>

                        </TabPane>
                        <TabPane tab={<span style={{display:'flex',flexDirection:'column'}}> {resize ? <UserOutlined />: ''}  <div style={resize ? {fontSize:'10pt'}:{}}>Profile</div></span>} key='3'>

                    </TabPane>
                        <TabPane tab={<span style={{display:'flex',flexDirection:'column'}}> {resize ? <RiseOutlined />: ''}  <div style={resize ? {fontSize:'10pt'}:{}}>Raiting</div></span>} key='4'>

                    </TabPane>
                    
                    </Tabs>
                    </Col>
                    </Row>
                </Content>
            </Layout>
    )
}